  font-weight: 600;
  text-decoration: none;
  transition: color .2s, transform .12s;
  position: relative;
}

.nav-menu li a:after {
  content: "";
  background: var(--accent);
  height: 3px;
  transition: transform .25s;
  position: absolute;
  bottom: 6px;
  left: 14px;
  right: 14px;
  transform: scaleX(0);
}

.nav-menu li a:hover, .nav-menu li a:focus-visible {
  color: var(--accent);
}

.nav-menu li a:hover:after, .nav-menu li a:focus-visible:after, .nav-menu li a.active:after {
  transform: scaleX(1);
}

.hamburger {
  cursor: pointer;
  border-radius: 10px;
  flex-direction: column;
  gap: 7px;
  padding: 12px;
  display: none;
}

